@import '../../styles/config/_queries';


.container {
  position: sticky;
  bottom: 100px;
  margin-top: auto;
  // fix Firefox behave differently on collapsing margins
  display: table;

  & &--center {
    margin-bottom: auto;
  }
}

.flexWrapper {
  display: flex;
  height: 100%;
}

@media (max-width: $breakpoint-md) {
  .container {
    margin: 0 2rem;
  }
}